.container {
    display: grid;
    position: relative;
    grid-template-rows: 1fr 10fr 1fr;
    width: 70%;
    background-color: #1f2633;
    border-radius: 20px;
    border: 7px solid #333;
    box-shadow: 0px 0px 15px 6px rgba(0, 0, 0, 0.2);
    z-index: 999;

    .content {
        display: grid;
        padding: 0 20px; } }

@media ( max-width: 769px ) {
    .container {
        width: 100%; } }

